<template>
    <div class="map_container" :style="{backgroundImage: 'url(' + albumpic_big + ')'}" ref="mapContain">
        <div id="mapOne" style="height:100%;width:100%;position: absolute;left:0;top:10px;opacity:0.5"></div>
        <div id="mapTwo" style="height:100%;width:100%;position: absolute;left:0;top:2px;opacity:0.5"></div>
        <div id="chinaMap" style="height:100%;width:100%;position: absolute;left:0;top:-6px;"></div>
        <div class="back_china" v-show="showChina" @click="backChina"></div>
    </div>
</template>
<script>
    import echarts from 'echarts';
    import _ from 'lodash'
    import china from '../../../../map/china.json';
    import shanghai from '../../../../map/json/province/shanghai';
    import beijing from '../../../../map/json/province/beijing'
    import hainan from "../../../../map/json/province/hainan"
    import xizang from '../../../../map/json/province/xizang'
    import zhejiang from '../../../../map/json/province/zhejiang'
    import yunnan from '../../../../map/json/province/yunnan'
    import xinjiang from '../../../../map/json/province/xinjiang'
    import tianjin from '../../../../map/json/province/tianjin'
    import sichuan from '../../../../map/json/province/sichuan'
    import shanxi from '../../../../map/json/province/shanxi'
    import shanxi1 from '../../../../map/json/province/shanxi1'
    import shandong from '../../../../map/json/province/shandong'
    import qinghai from '../../../../map/json/province/qinghai'
    import ningxia from '../../../../map/json/province/ningxia'
    import neimenggu from '../../../../map/json/province/neimenggu'
    import jilin from '../../../../map/json/province/jilin'
    import jiangxi from '../../../../map/json/province/jiangxi'
    import jiangsu from '../../../../map/json/province/jiangsu'
    import henan from '../../../../map/json/province/henan'
    import hunan from '../../../../map/json/province/hunan'
    import hubei from '../../../../map/json/province/hubei'
    import heilongjiang from '../../../../map/json/province/heilongjiang'
    import hebei from '../../../../map/json/province/hebei'
    import guangdong from '../../../../map/json/province/guangdong'
    import gansu from '../../../../map/json/province/gansu'
    import chongqing from '../../../../map/json/province/chongqing'
    import aomen from '../../../../map/json/province/aomen'
    import liaoning from '../../../../map/json/province/liaoning'
    import anhui from '../../../../map/json/province/anhui'
    import guangxi from '../../../../map/json/province/guangxi'
    import fujian from '../../../../map/json/province/fujian'
    import xianggang from '../../../../map/json/province/xianggang'
    import guizhou from '../../../../map/json/province/guizhou'
    import taiwan from '../../../../map/json/province/taiwan'
    import api from '@/api/chinaDataList.json'
    import cityApi from '@/api/chinaCityList.json'
    import pic from '../../../../assets/images/bg_line.png'

    function autoChart(chart, option,that) {
        //启动定时器前取消所有的高亮
        chart.dispatchAction({
                type: 'downplay',
                seriesIndex: 1,
        });
        const interval = setInterval( ()=> {
            var dataLen = option.series[1].data.length;
            var currentIndex = that.active =='china'? 'chinaCurrentIndex':'cityCurrentIndex'
            // 取消上一次高亮的图形
            chart.dispatchAction({
                type: 'downplay',
                seriesIndex: 1
            });
            // 高亮当前图形
            chart.dispatchAction({
                type: 'highlight',
                seriesIndex: 1,
                dataIndex: that[currentIndex]
            });
            // 显示 tooltip
            chart.dispatchAction({
                type: 'showTip',
                seriesIndex: 1,
                dataIndex: that[currentIndex]
            });
            that[currentIndex] = (that[currentIndex] + 1) % dataLen;
        }, 3000);
        return function () {
            clearInterval(interval)
        }
    }

    export default {
        props: {
            className: {
                type: String,
                default: 'mapChart'
            },
            id: {
                type: String,
                default: 'mapChart'
            },
            width: {
                type: String,
                default: '100%'
            },
            height: {
                type: String,
                default: '100%'
            }
        },
        data() {
            return {
                chinaCurrentIndex: 0,
                cityCurrentIndex: 0,
                albumpic_big:pic,
                mapChart: null,
                active: 'china',
                showChina: false,
                cityMap: {
                    'china': {
                        name: '中国',
                        data: china,
                        code: 'china'
                    },
                    'beijing': {
                        name: '北京',
                        data: beijing,
                        code: 'beijing'
                    },
                    'hainan': {
                        name: '海南',
                        data: hainan,
                        code: 'hainan'
                    },
                    'shanghai': {
                        name: '上海',
                        data: shanghai,
                        code: 'shanghai'
                    },
                    'hebei': {
                        name: '河北',
                        data: hebei,
                        code: 'hebei'
                    },
                    'shanxi': {
                        name: '山西',
                        data: shanxi,
                        code: 'shanxi'
                    },
                    "shanxi1": {
                        name: '陕西',
                        data: shanxi1,
                        code: 'shanxi1'
                    },
                    'xinjiang': {
                        name: '新疆',
                        data: xinjiang,
                        code: 'xinjiang'
                    },
                    'xizang': {
                        name: '西藏',
                        data: xizang,
                        code: 'xizang'
                    },
                    'zhejiang': {
                        name: '浙江',
                        data: zhejiang,
                        code: 'zhejiang'
                    },
                    'yunnan': {
                        name: '云南',
                        data: yunnan,
                        code: 'yunnan'
                    },
                    'tianjin': {
                        name: '天津',
                        data: tianjin,
                        code: 'tianjin'
                    },
                    'sichuan': {
                        name: '四川',
                        data: sichuan,
                        code: 'sichuan'
                    },
                    'shandong': {
                        name: '山东',
                        data: shandong,
                        code: 'shandong'
                    },
                    'qinghai': {
                        name: '青海',
                        data: qinghai,
                        code: 'qinghai'
                    },
                    'ningxia': {
                        name: '宁夏',
                        data: ningxia,
                        code: 'ningxia'
                    },
                    'neimenggu': {
                        name: '内蒙古',
                        data: neimenggu,
                        code: 'neimenggu'
                    },
                    'jilin': {
                        name: '吉林',
                        data: jilin,
                        code: 'jilin'
                    },
                    'jiangxi': {
                        name: '江西',
                        data: jiangxi,
                        code: 'jiangxi'
                    },
                    'jiangsu': {
                        name: '江苏',
                        data: jiangsu,
                        code: 'jiangsu'
                    },
                    'henan': {
                        name: '河南',
                        data: henan,
                        code: 'henan'
                    },
                    'hunan': {
                        name: '湖南',
                        data: hunan,
                        code: 'hunan'
                    },
                    'hubei': {
                        name: '湖北',
                        data: hubei,
                        code: 'hubei'
                    },
                    'heilongjiang': {
                        name: '黑龙江',
                        data: heilongjiang,
                        code: 'heilongjiang'
                    },
                    'guangdong': {
                        name: '广东',
                        data: guangdong,
                        code: 'guangdong'
                    },
                    'gansu': {
                        name: '甘肃',
                        data: gansu,
                        code: 'gansu'
                    },
                    'chongqing': {
                        name: '重庆',
                        data: chongqing,
                        code: 'chongqing'
                    },
                    'aomen': {
                        name: '澳门',
                        data: aomen,
                        code: 'aomen'
                    },
                    'liaoning': {
                        name: '辽宁',
                        data: liaoning,
                        code: 'liaoning'
                    },
                    'anhui': {
                        name: '安徽',
                        data: anhui,
                        code: 'anhui'
                    },
                    'guangxi': {
                        name: '广西',
                        data: guangxi,
                        code: 'guangxi'
                    },
                    'fujian': {
                        name: '福建',
                        data: fujian,
                        code: 'fujian'
                    },
                    'xianggang': {
                        name: '香港',
                        data: xianggang,
                        code: 'xianggang'
                    },
                    'guizhou': {
                        name: '贵州',
                        data: guizhou,
                        code: 'guizhou'
                    },
                    'taiwan': {
                        name: '台湾',
                        data: taiwan,
                        code: 'taiwan'
                    }
                },
                chinaDatas: api.chinaProvinceList,
                defaultP: [
                    // {
                    //     name: '浙江',
                    //     value: 300,
                    //     devNum: '324.424',
                    //     copyTax: '98.57',
                    //     signCover: '98.57',
                    //     aveRsrp: '-49.67',
                    //     aveSinr: '14.23',
                    //     selected: true,
                    // }
                ],
                left: false,
                top: false,
                priority: 'top',
                clearLoop: null
            };
        },
        methods: {
            initChart() {
                let _this = this;
                let mapOne = echarts.init(document.getElementById('mapOne'));
                let mapTwo = echarts.init(document.getElementById('mapTwo'))
                if(!this.mapChart){
                    this.mapChart = echarts.init(document.getElementById('chinaMap'));
                    // 添加事件
                    this.mapChart.on('click',  (params)=> {
                        _this.$refs.mapContain.style.backgroundImage ='none';
                        if (params.seriesType === 'map' && params.data) {
                            const selectData = convertData([{
                                name: params.name,
                                value: params.data.value,
                                devNum: params.data.devNum,
                                copyTax: params.data.copyTax,
                                signCover: params.data.signCover,
                                aveRsrp: params.data.aveRsrp,
                            }])
                            options.geo.regions[0] = {
                                name: params.name,
                                // selected: true
                            }
                            //显示圆点
                            options.series[0].data = selectData;
                            //显示省份地图
                            let item = _.find(_this.cityMap, (item) => {
                                return item.name === params.name
                            })
                            _this.active = item.code;
                            _this.showChina = true;
                            this.mapChart.dispatchAction({
                                type: 'downplay',
                                seriesIndex: 1
                            });
                            _this.cityCurrentIndex = 0
                            _this.initChart()
                        }
                    });
                    this.mapChart.on("mouseover",  (params)=>{
                        if(!params.data){
                            this.mapChart.dispatchAction({
                                type: 'downplay'
                            });
                        }else{
                            if(this.clearLoop){
                                this.clearLoop()
                                this.clearLoop = null;
                            }
                            this.mapChart.dispatchAction({
                                type: 'downplay',
                                seriesIndex: 1,
                            });
                            this.mapChart.dispatchAction({
                                type: 'highlight',
                                name: params.name,
                            });
                        }
                    });
                    this.mapChart.on("mouseout",  (params)=>{
                        if(params.data){
                            const option = this.mapChart.getOption()
                            if(!this.clearLoop){
                                this.clearLoop = autoChart(this.mapChart, option,this)
                            }
                        }
                    });
                }
                var convertData = function (data) {
                    var res = [];
                    for (var i = 0; i < data.length; i++) {
                        var geoCoord = api.geoCoordMap[data[i].name];
                        if (geoCoord) {
                            res.push({
                                name: data[i].name,
                                value: geoCoord.concat(data[i].value),
                                devNum: geoCoord.concat(data[i].devNum),
                                copyTax: geoCoord.concat(data[i].copyTax),
                                signCover: geoCoord.concat(data[i].signCover),
                                aveRsrp: geoCoord.concat(data[i].aveRsrp),
                                aveSinr: geoCoord.concat(data[i].aveSinr),
                            })
                        }
                    }
                    return res;
                };

                var convertCity = function (data) {
                    var res = [];
                    //su 修改
                    for(var i in data){
                            res.push({
                                name: data[i].name.indexOf('地区')>-1?data[i].name:data[i].name+'市',
                                value:1212121,
                            })
                    }
                    // for (var i = 0; i < data.length; i++) {
                    //     let geoCoord = _.find(_this.cityMap, (item) => {
                    //         return data[i].name === item.name
                    //     })
                    //     if (geoCoord) {
                    //         res.push({
                    //             name: data[i].name,
                    //             value: geoCoord.data.features[0].properties.cp.concat(data[i].value),
                    //             devNum: geoCoord.data.features[0].properties.cp.concat(data[i].devNum),
                    //             copyTax: geoCoord.data.features[0].properties.cp.concat(data[i].copyTax),
                    //             signCover: geoCoord.data.features[0].properties.cp.concat(data[i].signCover),
                    //             aveRsrp: geoCoord.data.features[0].properties.cp.concat(data[i].aveRsrp),
                    //             aveSinr: geoCoord.data.features[0].properties.cp.concat(data[i].aveSinr),
                    //         })
                    //     }
                    // }
                    return res;
                };

                echarts.registerMap(this.cityMap[this.active].code, this.cityMap[this.active].data, {});

                var mapData = _this.active === 'china' ? convertData(this.chinaDatas) : convertCity(cityApi[_this.active])
                let options = {
                    tooltip: {
                        show: true,
                        trigger: 'item',
                        triggerOn:'mousemove',
                        backgroundColor: 'transparent',
                        alwaysShowContent: true,
                        formatter: function (params) {
                            if (_this.active === 'china') {
                                var tipHtml = '';
                                if (params.data) {
                                    tipHtml += '<div class="province_div"></span><div class="border"><span>' + params.data.name + '</span></div></div>';
                                    if(Number(params.data.copyTax[2].split('%').join('')) <= 85){
                                        tipHtml += '<div class="number_div">'
                                            + '<div class="deviceNum"><span>设备数量</span><em>' + params.data.devNum[2] + '</em></div>'+
                                            '<div class="deviceNum"><span>抄表率</span><em style="color:red;">' + params.data.copyTax[2] + '</em></div>'+
                                            '<div class="deviceNum"><span>信号覆盖</span><em>' + params.data.signCover[2] + '</em></div>'+
                                            '<div class="deviceNum"><span>平均RSRP</span><em>' + params.data.aveRsrp[2] +'</em></div>'+
                                            '<div class="deviceNum"><span>平均SINR</span><em>' + params.data.aveSinr[2] + '</em></div>'+
                                            '</div>';
                                    }else if(Number(params.data.aveSinr[2]) <= 10){
                                        tipHtml += '<div class="number_div">'
                                            + '<div class="deviceNum"><span>设备数量</span><em>' + params.data.devNum[2] + '</em></div>'+
                                            '<div class="deviceNum"><span>抄表率</span><em>' + params.data.copyTax[2] + '</em></div>'+
                                            '<div class="deviceNum"><span>信号覆盖</span><em>' + params.data.signCover[2] + '</em></div>'+
                                            '<div class="deviceNum"><span>平均RSRP</span><em>' + params.data.aveRsrp[2] +'</em></div>'+
                                            '<div class="deviceNum"><span>平均SINR</span><em style="color:red;">' + params.data.aveSinr[2] + '</em></div>'+
                                            '</div>';
                                    }else /*if(Number(params.data.aveRsrp[2])< -90){
                                        tipHtml += '<div class="number_div">'
                                            + '<div class="deviceNum"><span>设备数量</span><em>' + params.data.devNum[2] + '</em></div>'+
                                            '<div class="deviceNum"><span>抄表率</span><em>' + params.data.copyTax[2] + '</em></div>'+
                                            '<div class="deviceNum"><span>信号覆盖</span><em>' + params.data.signCover[2] + '</em></div>'+
                                            '<div class="deviceNum"><span>平均RSRP</span><em style="color:red;">' + params.data.aveRsrp[2] +'</em></div>'+
                                            '<div class="deviceNum"><span>平均SINR</span><em>' + params.data.aveSinr[2] + '</em></div>'+
                                            '</div>';
                                    } else*/{
                                        tipHtml += '<div class="number_div">'
                                            + '<div class="deviceNum"><span>设备数量</span><em>' + params.data.devNum[2] + '</em></div>'+
                                            '<div class="deviceNum"><span>抄表率</span><em>' + params.data.copyTax[2] + '</em></div>'+
                                            '<div class="deviceNum"><span>信号覆盖</span><em>' + params.data.signCover[2] + '</em></div>'+
                                            '<div class="deviceNum"><span>平均RSRP</span><em>' + params.data.aveRsrp[2] +'</em></div>'+
                                            '<div class="deviceNum"><span>平均SINR</span><em>' + params.data.aveSinr[2] + '</em></div>'+
                                            '</div>';
                                    }

                                    return tipHtml;
                                } else {
                                    return;
                                }
                            } else {
                                var cityName = params.name.slice(0, -1);
                                var cityInfo = cityApi[_this.active][cityName]
                                if (cityInfo) {
                                    var temp = '';
                                    temp += '<div class="province_div"></span><div class="border"><span>' + params.name + '</span></div></div>';
                                    temp += '<div class="number_div">'
                                        + '<div class="deviceNum"><span>设备数量</span><em>' + cityInfo.devNum + '</em></div>' +
                                        '<div class="deviceNum"><span>抄表率</span><em>' + cityInfo.copyTax + '</em></div>' +
                                        '<div class="deviceNum"><span>信号覆盖</span><em>' + cityInfo.signCover + '</em></div>' +
                                        '<div class="deviceNum"><span>平均RSRP</span><em>' + cityInfo.aveRsrp + '</em></div>' +
                                        '<div class="deviceNum"><span>平均SINR</span><em>' + cityInfo.aveSinr + '</em></div>' +
                                        '</div>';
                                    return temp;
                                } else {
                                    return;
                                }
                            }
                        }
                    },
                    geo: {
                        map: this.active, // 表示中国地图
                        roam: false,
                        goDown: true, // 是否下钻
                        aspectScale: 0.8,//这个参数用于 scale 地图的长宽比。最终的 aspect 的计算方式是：geoBoundingRect.width / geoBoundingRect.height * aspectScale
                        zoom: 1.2,//当前视角的缩放比例。
                        label: {
                            normal: {
                                show: true,
                                textStyle: {
                                    color: 'rgba(58,175,255,1)',
                                    fontSize: 14,
                                    lineHeight: 19,
                                    align: 'center',
                                },
                            },
                            emphasis: {
                                textStyle: {
                                    color: 'rgba(255,255,255,1)',//省字体颜色
                                    fontSize: 20,
                                    lineHeight: 26,
                                },
                                symbolSize: 14,
                            },
                            rich: {
                                align: 'center',
                                verticalAlign: 'middle',
                            }
                        },
                        itemStyle: {
                            normal: {
                                borderColor: 'rgba(44,116,214,1)', //省份边框颜色
                                borderWidth: 1, //区域边框宽度
                                areaColor: '#164F99',//地图区域颜色
                                label: {
                                    show: true
                                },
                            },
                            emphasis: {
                                areaColor: '#3AAFFF',//鼠标滑过地区颜色
                            },
                        },
                        regions: this.defaultP,
                    },
                    series: [{
                        type: 'effectScatter',
                        coordinateSystem: 'geo',
                        z: 1,
                        data: convertData(this.defaultP),
                        symbol: 'circle',
                        symbolSize: [6, 4],
                        showEffectOn: 'render',
                        rippleEffect: {
                            brushType: 'stroke'
                        },
                        hoverAnimation: true,
                        itemStyle: {
                            color: 'transparent',
                            //length: 10,
                            //shadowBlur: 15,
                           // shadowColor: '#fff'
                        },
                        tooltip: {
                            show: true
                        }
                    }, {
                        type: 'map',
                        mapType: 'china',
                        geoIndex: 0,
                        z: 0,
                        coordinateSystem: 'geo', // 对应上方配置
                        selectedMode: 'single', //multiple多选
                        mapLocation: {
                            x: "10%",
                            y: "center"
                        },
                        data: mapData,
                    }]
                };
                this.mapChart.setOption(options)

                // const option = this.mapChart.getOption()
                if(this.clearLoop){
                    this.clearLoop()
                    this.clearLoop = null
                }
                this.clearLoop = autoChart(this.mapChart, options,this)

                // window.onresize = this.mapChart.resize()

                mapOne.setOption({
                    geo: {
                        map: this.active, // 表示中国地图
                        roam: false,
                        goDown: true, // 是否下钻
                        aspectScale: 0.8,//这个参数用于 scale 地图的长宽比。最终的 aspect 的计算方式是：geoBoundingRect.width / geoBoundingRect.height * aspectScale
                        zoom: 1.2,//当前视角的缩放比例。
                        itemStyle: {
                            normal: {
                                show: false,
                                borderColor: 'rgba(44,116,214,.5)', //省份边框颜色
                                borderWidth: 1, //区域边框宽度
                                areaColor: '#164F99',//地图区域颜色
                                opacity: .5,
                            }
                        },
                        regions: [{// 隐藏南海诸岛
                            name: '南海诸岛',
                            itemStyle: {
                                opacity: 0,
                            }
                        }, { // 隐藏南海诸岛
                            name: '海南',
                            itemStyle: {
                                opacity: 0,
                            }
                        }, { // 隐藏南海诸岛
                            name: '台湾',
                            itemStyle: {
                                opacity: 0,
                            }
                        }],
                    },
                })
                mapTwo.setOption({
                    geo: {
                        map: this.active, // 表示中国地图
                        roam: false,//是否拖拽
                        goDown: true, // 是否下钻
                        aspectScale: 0.8,//这个参数用于 scale 地图的长宽比。最终的 aspect 的计算方式是：geoBoundingRect.width / geoBoundingRect.height * aspectScale
                        zoom: 1.2,//当前视角的缩放比例。
                        itemStyle: {
                            show: false,
                            normal: {
                                borderColor: 'rgba(44,116,214,.7)', //省份边框颜色
                                borderWidth: 1, //区域边框宽度
                                areaColor: '#164F99',//地图区域颜色
                                opacity: .7,
                            },
                            emphasis: {
                                areaColor: '#3AAFFF',//鼠标滑过地区颜色
                            },
                        },
                        regions: [{// 隐藏南海诸岛
                            name: '南海诸岛',
                            itemStyle: {
                                opacity: 0,
                            }
                        }, { // 隐藏南海诸岛
                            name: '海南',
                            itemStyle: {
                                opacity: 0,
                            }
                        }, { // 隐藏南海诸岛
                            name: '台湾',
                            itemStyle: {
                                opacity: 0,
                            }
                        }],
                    },
                })
            },
            backChina() {
                this.active = "china";
                this.$refs.mapContain.style.backgroundImage ='url("'+this.albumpic_big+'")';
                if(this.clearLoop){
                    this.clearLoop();
                    this.clearLoop = null;
                }
                this.initChart();
                this.showChina = false;
            }
        },
        mounted() {
            this.$refs.mapContain.style.backgroundImage ='url("'+this.albumpic_big+'")';
            this.initChart();
        },
        destroyed() {

        },
