<script>
    import api from '@/api/equipSignaStreng.json'
    export default {
        name: 'citySynthesizeTop',
        data(){
            return {
                showTop5:true,
                changeDataTimer:null,
                carouselIndex:0,
                citySynthesizeTop5:api.citySynthesizeTop5[0]
            }
        },
        mounted(){
           this.changeDataTimer =  setInterval(()=>{
                this.showTop5 = false;
                this.carouselIndex ++ 
                if(this.carouselIndex >=4){
                    this.carouselIndex =0                  
                }
                this.$nextTick(()=>{
                    this.citySynthesizeTop5 = api.citySynthesizeTop5[this.carouselIndex]
                    this.showTop5 = true;
                })
            },40000)
        },
        beforeDestroy(){
            clearInterval(this.changeDataTimer)
        }
    }
</script>

