<template>
    <div class="synthesize_last">
        <div class="block_div">
            <div class="border_left_top"></div>
            <div class="border_right_top"></div>
            <div class="border_right_bottom"></div>
            <div class="border_left_bottom"></div>
            <div class="block_title">
                <h4>
                    <i class="left_line"><img src="../../../../assets/images/city_line.svg"/></i>
                    <span>城市信号综合指数LAST5</span>
                    <i class="right_line"><img src="../../../../assets/images/city_line.svg"/></i>
                </h4>
            </div>
            <div class="block_content">
                <div class="carouse-table-header">
                    <div class="header_cell">
                        <div class="cell_index"></div>
                        <div class="cell_left">城市</div>
                        <div class="cell_center">综合指数</div>
                        <div class="cell_right">趋势</div>
                    </div>
                </div>
                <div class="carouse-table-body">
                    <swiper style="height:100%" :options="swiperOption">
                        <swiper-slide class="slide_cell" v-for="(slide, index) in citySynthLastData" :key="index">
                            <div class="silde_body">
                                <div class="cell_index"><span class="index">{{index+1}}</span></div>
                                <div class="cell_left">{{slide.cityName}}</div>
                                <div class="cell_center">{{slide.compreIndex}}</div>
                                <div class="cell_right">
                                    <span v-if="slide.tend == 0"><img src="../../../../assets/images/chiping.svg"></span>
                                    <span v-if="slide.tend == 1"><img src="../../../../assets/images/up.svg"></span>
                                    <span v-if="slide.tend == 2"><img src="../../../../assets/images/down.svg"></span>
                                </div>
                            </div>
                        </swiper-slide>
                    </swiper>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import { swiper, swiperSlide } from 'vue-awesome-swiper'
    import api from '@/api/equipSignaStreng.json'
    export default {
        name: 'citySynthesizeLAST',
        components:{
            swiper,swiperSlide
        },
        data(){
            return {
                swiperOption: {                     //swiper无缝滚动配置项
                    direction: "vertical",
                    autoplay: true,
                    slidesPerView:5
                },
                citySynthLastData:[],

            }

        },
        methods:{
            citySynthLastList(){
                this.citySynthLastData = api.citySynthLastData.slice(0,5);
                let index = api.citySynthLastData.length-1;
                let self = this;
                setInterval(function () {
                    self.toFirst(api.citySynthLastData,index)
                    self.citySynthLastData = api.citySynthLastData.slice(0,5)
                },3000)
            },
            toFirst(fieldData,index) {
                if(index!=0){
                    // fieldData[index] = fieldData.splice(0, 1, fieldData[index])[0]; 这种方法是与另一个元素交换了位子，
                    fieldData.unshift(fieldData.splice(index , 1)[0]);
                }
            },

        },
        mounted() {
            this.citySynthLastList()
        }



    }
</script>

